.wp-block-create-block-nextcloud-block-plugin {

	padding: 2px;
	font-size: 14px;

}
.file-tree {
	padding:0px;
	margin:0px;
}
.file-tree li.node {
	cursor: pointer;
	user-select: none;
	box-sizing: border-box;;
	margin-top: 0px;
	list-style: none;
}
.file-tree span {
	font-family: 'Open Sans', sans-serif;
}

.file-tree .node.directory::before {
	content: "\1F4C1";
	display: inline-block;
	margin-right: 6px;
}
.file-tree .node.file::before {
	content: "\1F5CE";
	display: inline-block;
	margin-right: 16px;
	transform: scale(1.4);
}


.file-tree .directory > span{
	font-weight: bold;
}
.file-tree .inverted-text-color, .inverted-text-color .file::before   {
	/* Invertierte Farbe */
	color: white;
}
.file-tree .node.file{
	margin-left: 5px;
}

.file-tree .open .node.directory::before {
	content: "\1F4C2";
	display: inline-block;
	margin-right: 6px;
}
.file-tree .node-children {
	display: block;
	max-height: 0px;
	overflow: hidden;
	transition: all 0.5s ease-in-out;
	margin-left: 12px;
}

.file-tree .active {
	max-height:1000px;
}
